.bg-fixed {
  background-attachment: fixed;
}
.bg-image {
  position: relative;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;
}
.mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-attachment: fixed;
}
.hover-overlay .mask {
  --mdb-image-hover-transition: all 0.3s ease-in-out;
  opacity: 0;
  transition: var(--mdb-image-hover-transition);
}
.hover-overlay .mask:hover {
  opacity: 1;
}
.hover-zoom {
  --mdb-image-hover-zoom-transition: all 0.3s linear;
  --mdb-image-hover-zoom-transform: scale(1.1);
}
.hover-zoom img,
.hover-zoom video {
  transition: var(--mdb-image-hover-zoom-transition);
}
.hover-zoom:hover img,
.hover-zoom:hover video {
  transform: var(--mdb-image-hover-zoom-transform);
}
.card.hover-shadow,
.card.hover-shadow-soft,
.hover-shadow,
.hover-shadow-soft {
  --mdb-image-hover-shadow-transition: all 0.3s ease-in-out;
  transition: var(--mdb-image-hover-shadow-transition);
}
.card.hover-shadow-soft:hover,
.card.hover-shadow:hover,
.hover-shadow-soft:hover,
.hover-shadow:hover {
  transition: var(--mdb-image-hover-shadow-transition);
}
.card.hover-shadow,
.hover-shadow {
  --mdb-image-hover-shadow-box-shadow: 0 2px 15px -3px rgba(0, 0, 0, 0.16),
    0 10px 20px -2px rgba(0, 0, 0, 0.1);
  box-shadow: none;
}
.card.hover-shadow:hover,
.hover-shadow:hover {
  box-shadow: var(--mdb-image-hover-shadow-box-shadow);
}
.card.hover-shadow-soft,
.hover-shadow-soft {
  --mdb-image-hover-shadow-box-shadow-soft: 0 2px 25px -5px rgba(0, 0, 0, 0.07),
    0 25px 21px -5px rgba(0, 0, 0, 0.04);
  box-shadow: none;
}
.card.hover-shadow-soft:hover,
.hover-shadow-soft:hover {
  box-shadow: var(--mdb-image-hover-shadow-box-shadow-soft);
}
.form-control {
  min-height: auto;
  padding: 4.5px 12px 3.68px;
}
.form-control,
.form-control:focus {
  transition: all 0.1s linear;
  box-shadow: none;
}
.form-control:focus {
  border-color: #3b71ca;
  box-shadow: inset 0 0 0 1px #3b71ca;
}
.form-control.form-control-sm {
  font-size: 0.775rem;
  line-height: 1.5;
}
.form-control.form-control-lg {
  line-height: 2.15;
  border-radius: 0.25rem;
}
.form-outline {
